
.page-my {
  height: 100%;
  position: relative;
}
.page-my .logout-button {
  background-color: #fff;
  margin-top: .4rem;
  height: 1.333333rem;
}
.page-my__content {
  position: absolute;
  top: 1.333333rem;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  background-color: #f2f2f2;
  box-sizing: border-box;
  padding: .266667rem .2rem;
}
.page-my__content .weui-cell {
  height: 1.333333rem;
  line-height: 1.333333rem;
  box-sizing: border-box;
  padding: 0 .533333rem;
}
.page-my__content-info {
  background-color: #fff;
  height: 5.066667rem;
  border-radius: .08rem;
  overflow: hidden;
}
.page-my__content-pic {
  text-align: center;
  margin-top: .666667rem;
  font-size: 0;
}
.page-my__content-pic i {
  display: inline-block;
  height: 2.053333rem;
  width: 2.053333rem;
  border-radius: 50%;
  background: url("~@/assets/news2.png") no-repeat;
  background-size: cover;
}
.page-my__content-mobile {
  text-align: center;
  font-size: .64rem;
  margin-top: .453333rem;
}
.page-my__content-card {
  text-align: center;
  font-size: .4rem;
  color: #666;
}



