
body {
  margin: 0;
}
.page-login {
  background-color: #fff;
  height: 100%;
  overflow: hidden;
}
.page-login__logo {
  height: 5.333333rem;
  background: url("~@/assets/logo.png") no-repeat center center;
  background-size: 3.333333rem;
}
.page-login__form {
  padding: 0 .906667rem;
}
.page-login__button {
  margin-top: .266667rem;
}
.page-login__bottom {
  text-align: center;
  margin-top: .533333rem;
  font-size: .4rem;
}
.page-login__bottom a {
  margin: 0 .133333rem;
}



